import React from 'react'
import './thanksPage.css';
import Hero from '../hero/hero';
import { Grid } from '@mui/material';
import ReactPlayer from 'react-player';
import '../fonts/Revital.ttf'
import { useMediaQuery } from 'react-responsive';

const ThanksPage = (props) => {

  const isDesktop = useMediaQuery({
    query: "(min-device-width: 1200px)",
  });

    return(
      isDesktop?
      <Hero backgroundColor = "#EDEDE5">
          <Grid container>
              <Grid item xs = {12}>
                    <p className='thanksHeader'>אז למה לתעד את הסיפור חיים שלכם<br></br>
                  <span className='thanksHandwritten'>ולמה דוקו-מי הוא המקום האידיאלי בשבילכם לעשות זאת</span></p> 
              </Grid>
              <Grid item xs = {3}>
              
              </Grid>
              <Grid item xs = {6} style = {{width: '50vw', height:'60vh'}}>
                <ReactPlayer url='https://www.youtube.com/watch?v=RooDETdsaVg' width="100%" height="100%"/>
              </Grid>
              <Grid item xs = {3}>
              
              </Grid>
          </Grid>
      </Hero>
      :
      <Hero backgroundColor = "#EDEDE5">
          <Grid container>
              <Grid item xs = {12}>
                    <p className='thanksHeaderMobile'>אז למה לתעד את הסיפור חיים שלכם<br></br>
                  <span className='thanksHandwrittenMobile'>ולמה דוקו-מי הוא המקום האידיאלי בשבילכם לעשות זאת</span></p> 
              </Grid>
              <Grid item xs = {1}>
              
              </Grid>
              <Grid item xs = {10} style = {{height:'40vh', marginBottom : '3vh'}}>
                <ReactPlayer url='https://www.youtube.com/watch?v=RooDETdsaVg' width="100%" height="100%"/>
              </Grid>
              <Grid item xs = {1}>
              
              </Grid>
          </Grid>
      </Hero>
    )
    
}

export default ThanksPage