import React from 'react'
import Button from '@mui/material/Button'
import './DetailsButton.css'
import { alpha, styled } from '@mui/material/styles';
import { useMediaQuery } from 'react-responsive';
import { Typography } from '@mui/material';

const DetailsButton = (props) => {
    const DetailsButton = styled(Button)(({ theme }) => ({
           color: 'white',
           backgroundColor: '#F57854',
          '&:hover': {         
            backgroundColor : '#FF825E'
          },
      }));

      const isDesktop = useMediaQuery({
        query: "(min-device-width: 1200px)",
    });

    return(
      isDesktop?
        <DetailsButton  variant='contained' style = {{borderRadius:'1vw',padding:'5px' ,direction : 'rtl', width:'100%',}}><Typography style = {{fontSize: '1.5vw', fontFamily : 'Segoe UI'}}>{props.text}</Typography></DetailsButton>
        :
        <DetailsButton  variant='contained' style = {{borderRadius:'1vw',padding:'2vw'}}><Typography style = {{fontSize: '2vh', fontFamily : 'Segoe UI'}}>כן! אשמח לקבל פרטים נוספים</Typography></DetailsButton>
    )
}

export default DetailsButton