import React from 'react'
import './hero.css';
import { useMediaQuery } from 'react-responsive';


const Hero = (props) => {

    const isDesktop = useMediaQuery({
        query: "(min-device-width: 1200px)",
    });

    return(
        isDesktop?
        <div style = {{backgroundColor : props.backgroundColor ? props.backgroundColor : "none" , height : props.height ? props.height : "100vh", width : props.width ? props.width : "100vw"}}> 
            {props.children}
        </div>
        :
        <div style = {{backgroundColor : props.backgroundColor ? props.backgroundColor : "none" , width : props.width ? props.width : "100vw"}}> 
            {props.children}
        </div>
    )
}

export default Hero