import React from 'react'
import BackgroundImageContainer from './backgroundImageContainer/backgroundImageContainer'
import Hero from '../hero/hero'
import Grid from '@mui/material/Grid'
import './homePage.css'
import DetailsButton from '../DetailsButton/DetailsButton'
import Logo from '../Images/Logo.png'
import { useMediaQuery } from 'react-responsive';
import HomePageBackgroundMobile from './ImagesMobile/HomePageBackgroundMobile.jpg'

const HomePage = (props) => {

    const isDesktop = useMediaQuery({
        query: "(min-device-width: 1200px)",
    });

    return(
        isDesktop?
        <Hero>
                <BackgroundImageContainer>
                    <Grid container>
                        <Grid item xs= {6}>
                            <Grid item xs = {5} style = {{marginTop:'3vh'}}>
                                <img src = {Logo} style = {{width:'100%'}}></img>
                            </Grid>
                        </Grid>
                        <Grid item xs= {6}>
                            <Grid container>   
                                <Grid item xs = {3}></Grid>
                                <Grid item xs = {8} >
                                    <p className='Header'>אלבום סיפור חיים – בדיוק המתנה המקורית שחיפשתם</p>
                                </Grid>
                                <Grid item xs = {2}></Grid>
                                <Grid item xs = {9}>
                                    <p className='Subtext'>חוגגים יום הולדת לאדם אהוב?  <br></br>
                                    מרגישים שמיציתם את כל הרעיונות ה"שיגרתיים" למתנה <br></br>
    וכבר לא נשארו 
    יותר שפנים בכובע? <br></br>
    אז הגעתם למקום הנכון!<br></br>
    הפעם זו תהיה מתנה מקורית ומיוחדת כזאת שתרגש<br></br>
    ותעשה טוב על הלב. אלבום סיפור חיים במתנה הוא<br></br>
    אלבום גדול ומרהיב ביופיו המשלב בין ספר ואלבום<br></br>
    תמונות גדול. אלבום המתעד תחנות בסיפור החיים<br></br>
    של אדם קרוב ויקר בסיפורים, בחוויות ובתמונות מן<br></br>
    העבר. תוך שילוב צילומים אישיים ומשפחתיים עכשווים<br></br>
    שיוצרים יחד חוויה וזיכרונות משפחתיים חדשים.<br></br></p>
                                </Grid>
                                <Grid item xs = {4.5}></Grid>
                                <Grid item xs = {6} style = {{marginTop: '5vh'}}>
                                <DetailsButton text = "כן! אשמח לקבל פרטים נוספים"></DetailsButton>
                                </Grid>
                            </Grid>
                        </Grid>
                    
                    </Grid>
                </BackgroundImageContainer>  
            </Hero>
            :
            <Hero backgroundColor = "#FFD2A3" >
                <Grid container style = {{backgroundColor : "#FFD2A3"}}>
                    <Grid container item xs = {12} style = {{justifyContent:'center', backgroundColor : 'white'}}>
                        <img src = {Logo} style = {{width:'50%'}}></img>
                    </Grid>
                    <Grid container item xs = {12} style = {{justifyContent:'center'}}>
                        <img src = {HomePageBackgroundMobile} style = {{width:'100%'}}></img>
                    </Grid>
                    <Grid container item xs = {1} style = {{justifyContent:'center'}}>
                    
                    </Grid>
                    <Grid item xs = {10.5} >
                        <p className='HeaderMobile'>אלבום סיפור חיים – בדיוק המתנה המקורית שחיפשתם</p>
                    </Grid>
                    <Grid container item xs = {1} style = {{justifyContent:'center'}}>
                    
                    </Grid>
                    <Grid container item xs = {10.5} style = {{justifyContent:'center'}}>
                    <p className='SubtextMobile'>חוגגים יום הולדת לאדם אהוב?  
                                    מרגישים שמיציתם את כל הרעיונות ה"שיגרתיים"  
                                    למתנה וכבר לא נשארו 
                                    יותר שפנים בכובע? 
                                    אז הגעתם למקום הנכון!
                                    הפעם זו תהיה מתנה מקורית ומיוחדת 
                                    כזאת שתרגש ותעשה טוב על הלב. 
                                    {/* <br></br> */}
                                    אלבום סיפור חיים במתנה הוא אלבום גדול ומרהיב ביופיו המשלב בין ספר ואלבום תמונות גדול. 
                                    אלבום המתעד תחנות בסיפור החיים
                                    של אדם קרוב ויקר בסיפורים, בחוויות ובתמונות מן
                                    העבר. תוך שילוב צילומים אישיים ומשפחתיים 
                                    עכשווים שיוצרים יחד חוויה וזיכרונות משפחתיים חדשים.</p>
                   
      
        
                   
                    
                        <p className='SubtextMobile'>
                    כמה פעמים כבר שמעת את סיפורי החיים של אבא
                                        ואמא, וכל פעם זה כל כך מרגש, ואתה רוצה להנציח את                        
                                        הסיפורים האלה אחת ולתמיד, וכל פעם זה נדחה כי אין זמן, כי כולם עסוקים, ועוד שנה עוברת.  
                                        <br></br>

                    הינה מגיעה חגיגת יום ההולדת או יום הנישואין של 
                    ההורים וזו ההזדמנות לתת להם מתנה כזאת 
                    שתרגש אותם ואת כל בני המשפחה.
                    מתנה שתרכז ותתעד ברגישות רבה את אוסף הזיכרונות שלהם,
                    ותספר את הסיפור המיוחד שלהם, את מי שהם היו ואת מי שהם היום<br></br>
                    <span className='HomeSubtextHand'>אנחנו פה בשביל לגרום לזה לקרות</span>
                        </p>
                        </Grid>

                    <Grid item xs = {2} >
                
                </Grid>
                    <Grid container item xs = {8} style = {{marginTop: '5vh', marginBottom:'5vh', justifyContent:'center'}}>
                        <DetailsButton></DetailsButton>
                    </Grid>
                    <Grid item xs = {2}  >
                    </Grid>
          </Grid>
                    
           

    
    </Hero>
    
    )
}

export default HomePage